                user_id:"",
                limit:20,
                offset:0,
                hongbaos1:[],
                hongbaos2:[]
            }
        },
        created(){
            this.user_id = JSON.parse(localStorage.getItem('user_id'));
            // console.log(this.user_id);
            this.axios.get("/promotion/v2/users/" + this.user_id + "/hongbaos",{
                params:{
                    limit:this.limit,
                    offset:this.offset
                }
            }).then(res=>{
                // console.log(res);
                this.hongbaos1 = res.filter(item => item.limit_map)
                this.hongbaos2 = res.filter(item => item.limit_map == undefined)
            }).then(err=>{});
        }
    }
</script>

